<template>
    <div class="swiper-container" ref="mySwiper">
        <div class="swiper-wrapper">
            <slot></slot>
        </div>
        <!-- 如果需要分页器 -->
        <div class="swiper-pagination"></div>
    </div>
</template>

<script>
export default {
    name: "swiper",
    mounted() {
        new Swiper (this.$refs.mySwiper, {
			loop: true, // 循环模式选项
			autoplay: { // 可选选项，自动滑动
				disableOnInteraction: false,
			},
			delay: 2000,// 2秒切换一次
			speed: 800, // 切换速度
			effect: 'fade', // "fade"（淡入）"cube"（方块）"coverflow"（3d流）"flip"（3d翻转）
			fadeEffect: {
			    crossFade: true,
			},
			// 如果需要分页器
			pagination: {
			  el: '.swiper-pagination',
			  clickable: true
			},
			// 
			observer:true,
			observeParents:true,
		});
    },
}
</script>

<style scoped>
.swiper-container {
	width: 100%;
	min-height: 160px;
    max-height: 300px;
}
</style>